export default {
  data() {
    return {
      searchQuery: '',
      articles: []
    }
  },
  watch: {
    async searchQuery(searchQuery) {
      if (!searchQuery) {
        this.articles = []
        return
      }
      this.articles = await this.$content('articlesMDMA')
        .limit(6)
        .search(searchQuery)
        .fetch()
    }
  }
}
