export default {
  async asyncData({ $content }) {
    const articles = await $content('articlesMDMA')
      //.only(['titolo', 'autore', 'slug', 'parole', 'img', 'body'])
      .sortBy('titolo', 'asc')
      .fetch()

    return {
      articles
    }
  }
}
